.profile{
    padding: 20px;
    border: tomato 1px solid;
    width: 300px;
    margin-right: auto;
    margin-left: auto;
    border-radius: 5px;
}
.description{
    display: block;
    margin-right: auto;
    margin-left: auto;
}
.avatar{
    margin-right: auto;
    margin-left: auto;
    width: 150px;
    height: auto;
    display: block;
}
ul{
    margin: 0%;
    padding: 0%;
    list-style: none;
}
.name{
    margin-top: 0%;
    text-align: center;
}
.tag{
    margin-top: 0%;
    text-align: center;
}
.location{
    margin-top: 0%;
    text-align: center;
    margin-bottom: 0%;
}
.stats{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}
.stats>li:not(:last-child){
    margin-right: 20px;
}
.lable{
    margin-right: 20px;
    color: brown;
}
.lable:last-child{
    margin-right: 0%;
}
.quantity{
    margin-right: 20px;
    color: red;
    line-height: 1.8em;
}
.quantity:last-child{
    margin-right: 0%;
}